<template>
  <section class="main_con">
    <div class="hero-area border-none pd-0" v-if="data.photo">
      <a style="cursor: pointer" @click="buy()" v-if="!data.buyed">
        <img :src="data.price_photo" alt="" class="hero-b_img"
      /></a>
      <a
        style="cursor: pointer"
        @click="$router.push('/course-view/' + data.id)"
        v-if="data.buyed"
      >
        <img :src="data.photo" alt="" class="hero-b_img"
      /></a>
    </div>
    <!-- Hero-area END -->
    <!-- introTwo-area STARTED -->
    <div class="introTwo" v-if="data.photo">
      <div class="container">
        <div class="row">
          <div class="col-lg-5">
            <div class="iTwoVdo">
              <video width="100%" controls id="myvideo">
                <source :src="data.trailer" type="video/mp4" />
                <source src="rain.ogg" type="video/ogg" />
                Your browser does not support the video tag.
              </video>

              <img @click="fullVid" src="@/assets/img/Preview.png" alt="" />
            </div>
          </div>
          <div class="col-lg-7">
            <div class="iTwoVdo-wrp">
              <h2>{{ data.author_id.fio }}</h2>
              <p>{{ data.author_id.description }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- introTwo-area END -->

    <!-- course-area STARTED -->
    <div class="course-area">
      <img src="@/assets/img/secondpage/statcue.png" alt="" class="statuce" />
      <div class="container">
        <div class="row">
          <div class="col-md-10">
            <div class="course-wrp">
              <h2>ИДЕЯ КУРСА</h2>
              <p>{{ data.description }}</p>
            </div>
          </div>
          <div class="col-lg-12">
            <h1 class="d-block d-md-none dttl">СОДЕРЖАНИЕ</h1>
            <div class="details">
              <img
                src="@/assets/img/secondpage/frame.png"
                alt=""
                class="dtlimg"
              />
              <div class="cr-content">
                <h1 class="d-none d-md-block">СОДЕРЖАНИЕ</h1>
                <div class="cr-content-in">
                  
                  <ul>
                    <li v-for="(y, index) in data.lessons" v-bind:key="y.id">
                      <div v-if="index === 0" class="iconOf">
                        <img src="@/assets/img/secondpage/play.png" alt="" />
                      </div>
                      <div v-else class="iconOf">
                        <img src="@/assets/img/secondpage/lock.png" alt="" />
                      </div>
                      <div class="dltBLkWp">
                        <span>{{ index - 0 + 1 }} cерия</span>
                        <h4>{{ y.title }}</h4>
                        <p>{{ y.description }}</p>
                      </div>
                    </li>

                    <li v-for="(y, index) in data.lessons" v-bind:key="y.id">
                      <div v-if="index === 0" class="iconOf">
                        <img src="@/assets/img/secondpage/play.png" alt="" />
                      </div>

                      <div v-if="index === 0" class="dltBLkWp">
                        <span>{{ index - 0 + 1 }} cерия</span>
                        <h4>{{ y.title }}</h4>
                        <p>{{ y.description }}</p>
                      </div>
                    </li>

                    <li v-for="(y, index) in data.lessons" v-bind:key="y.id">
                      <div v-if="index === 0" class="iconOf">
                        <img src="@/assets/img/secondpage/play.png" alt="" />
                      </div>

                      <div v-if="index === 0" class="dltBLkWp">
                        <span>{{ index - 0 + 1 }} cерия</span>
                        <h4>{{ y.title }}</h4>
                        <p>{{ y.description }}</p>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- course-area END -->
  </section>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
export default {
  name: "Home",
  components: {},
  data() {
    return {
      data: [],
    };
  },

  mounted() {
    if (this.$route.params.id) {
      if (this.$store.state.token) {
        axios
          .post(
            "https://education-api.gnt-it.ru/v1/course/view",
            {
              id: this.$route.params.id,
            },
            {
              headers: {
                Authorization: `Bearer ${this.$store.state.token}`,
              },
            }
          )
          .then((response) => {
            this.data = response.data.data;

            return false;

            localStorage.setItem("token", response.data.user.token);

            this.$store.state.token = response.data.user.token;
            this.$router.push("/");
          })
          .catch((error) => {
            this.login_error = true;
          });
      } else {
        axios
          .post("https://education-api.gnt-it.ru/v1/course/view", {
            id: this.$route.params.id,
          })
          .then((response) => {
            this.data = response.data.data;
            return false;

            localStorage.setItem("token", response.data.user.token);

            this.$store.state.token = response.data.user.token;
            this.$router.push("/");
          })
          .catch((error) => {
            this.login_error = true;
          });
      }
    }

    window.scrollTo({
      top: 0,
      behavior: "instant",
    });
  },
  methods: {
    fullVid() {
      var elem = document.getElementById("myvideo");
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Safari */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE11 */
        elem.msRequestFullscreen();
      }
    },
    buy() {
      console.log(this.$store.state.token);
      if (!this.$store.state.token) {
        this.$router.push("/login");
        return true;
      }
      this.$store.state.byuingCur = this.$route.params.id;
      this.$router.push("/buy-course");
    },
  },
};
</script>

<style scoped>

