<script>
// @ is an alias to /src
import axios from "axios";

export default {
  name: "Home",
  components: {},
  data() {
    return {
      chartsEnabled: false,
      allData: [],
      date_now: null,
      vidosOpen: false,
      fields: [
        {
          key: "id",
          sortable: true,
          label: "#ID",
        },
        {
          key: "id",
          sortable: true,
          label: "#ID",
        },
        {
          key: "out_system_id",
          sortable: true,
          label: "# Заказа",
        },
        {
          key: "name",
          sortable: false,
          label: "Заказчик",
        },
        {
          key: "address",
          sortable: true,
          label: "Адрес",
        },
        {
          key: "delivery_time",
          sortable: true,
          label: "Время доставки",
        },
        {
          key: "created_at",
          sortable: false,
          label: "Дата создания",
        },
        {
          key: "delivery_fact_time",
          sortable: false,
          label: "Точное время доставки",
        },
        {
          key: "phone",
          sortable: false,
          label: "Телефон",
        },
        {
          key: "name",
          sortable: false,
          label: "Товар( в АПИ нет)",
        },
        {
          key: "status",
          sortable: true,
          label: "Статус",
        },
      ],
      courses: [],
      items: [],
    };
  },
  methods: {
    fullVid() {
      var elem = document.getElementById("myvideo");
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Safari */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE11 */
        elem.msRequestFullscreen();
      }
    },
    roCur(e) {
      document.getElementById(e).scrollIntoView();
    },
  },
  mounted() {
    const element = document.getElementById("courses");
    const offset = 0;
    const bodyRect = document.body.getBoundingClientRect().top;
    const elementRect = element.getBoundingClientRect().top;
    const elementPosition = elementRect;
    const offsetPosition = elementPosition - offset;

    if (this.$store.state.scrolerCur === true) {
      window.scrollTo({
        top: offsetPosition,
        behavior: "smooth",
      });
      this.$store.state.scrolerCur = false;
    }

    var today = new Date();
    this.date_now =
      today.getDate() +
      "." +
      (today.getMonth() + 1) +
      "." +
      today.getFullYear();

    axios
      .get("https://education-api.gnt-it.ru/v1/course/list", {
        headers: {
          Authorization: `Bearer ${this.$store.state.token}`,
        },
      })
      .then((response) => {
        console.log(response);
        this.courses = response.data.data;
        return false;

        localStorage.setItem("token", response.data.user.token);

        this.$store.state.token = response.data.user.token;
        this.$router.push("/");
      })
      .catch((error) => {
        this.login_error = true;
      });
  },
};
</script>
